<template>
  <v-layout>
    <MapDialogBox />
    <LoadingScreen v-if="isLoading" />
    <div id="map" relative class="map"></div>
    <v-card v-if="!isXs()" class="nav-drawer" max-width="300">
      <v-list rounded>
        <v-row>
          <v-col justify-left>
            <v-subheader>DATASET</v-subheader>
          </v-col>
        </v-row>
        <v-divider></v-divider>
        <v-list-item-group v-model="selectedDataset" color="primary">
          <v-list-item v-for="(dataset, i) in datasets" :key="i">
            <v-list-item-icon>
              <v-icon v-text="dataset.icon"></v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title v-text="dataset.text"></v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-card>
    <v-btn v-else @click="drawer = true" icon class="dataset-icon">
      <v-icon >mdi-database-marker</v-icon>
    </v-btn>

    <v-navigation-drawer v-model="drawer" fixed temporary>
      <v-list nav dense>
        <v-list-item-group
          active-class="primary--text text--accent-4"
        >
          <v-list-item>
            
            <v-list-item-title>DATASET</v-list-item-title>
          </v-list-item>

          <v-list-item @click="selectedDataset = 0; drawer = false">
            <v-list-item-icon>
              <v-icon>mdi-molecule-co2</v-icon>
            </v-list-item-icon>
            <v-list-item-title>CO2</v-list-item-title>
          </v-list-item>

          <v-list-item @click="selectedDataset = 1; drawer = false">
            <v-list-item-icon>
              <v-icon>mdi-thermometer</v-icon>
            </v-list-item-icon>
            <v-list-item-title>Temperature</v-list-item-title>
          </v-list-item>

          <v-list-item @click="selectedDataset = 2; drawer = false">
            <v-list-item-icon>
              <v-icon>mdi-water</v-icon>
            </v-list-item-icon>
            <v-list-item-title>Humidity</v-list-item-title>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </v-layout>
</template>

<script>
import Mapbox from "mapbox-gl";
import MapData from "../assets/data.json";
import MapLayers from "../assets/maplayers.json";
import MapDialogBox from "./MapDialogBox.vue";
import LoadingScreen from "./LoadingScreen.vue";
export default {
  name: "MapboxMap",
  components: {
    MapDialogBox,
    LoadingScreen
  },
  props: {
    accessToken: {
      required: true,
      type: String
    },
    options: {
      required: true,
      type: Object
    }
  },
  data() {
    return {
      selectedDataset: 0,
      displayDatasets: false,
      datasets: [
        { text: "CO2", icon: "mdi-molecule-co2" },
        { text: "Temperature", icon: "mdi-thermometer" },
        { text: "Humidity", icon: "mdi-water" }
      ],
      map: null,
      isLoading: true,
      drawer: false
    };
  },
  mounted() {
    this.generateMap();
    this.map.on("load", () => {
      this.isLoading = false;
      this.generateLayers();
      this.toggleLayer();
      console.log("LOADED");
      // this.renderMapboxUI();
    });
  },

  watch: {
    selectedDataset: {
      handler: function() {
        this.toggleLayer();
      },
      deep: true
    }
  },
  methods: {
    isXs() {
      return this.$vuetify.breakpoint.xsOnly ? true : false;
    },
    generateMap() {
      Mapbox.accessToken = this.accessToken;
      this.map = new Mapbox.Map(this.options);
    },
    renderMapboxUI() {
      this.map.addControl(
        new Mapbox.GeolocateControl({
          positionOptions: {
            enableHighAccuracy: true
          },
          trackUserLocation: true
        }),
        "bottom-right"
      );
      this.map.addControl(new Mapbox.NavigationControl(), "bottom-right");
    },
    generateLayers() {
      this.map.addSource("airquality", {
        type: "geojson",
        data: MapData
      });
      for (let i = 0; i < 3; i++) {
        this.map.addLayer(MapLayers.datasets[i].layers[0]);
        let propertyName = MapLayers.datasets[i].layers[0].id.slice(0, -5);
        let unit = "";
        this.map.on("click", MapLayers.datasets[i].layers[0].id, e => {
          let val = e.features[0].properties[propertyName];
          switch (i) {
            case 0:
              unit = " ppm";
              break;
            case 1:
              unit = "";
              val = (Math.round(val * 100) / 100) * 1.8 + 32;
              break;
            case 2:
              unit = "%";
              val = Math.round(val * 100) / 100;
              break;
          }
          new Mapbox.Popup()
            .setLngLat(e.features[0].geometry.coordinates)
            .setHTML("<b>" + this.datasets[i].text + ": " + val + unit + "</b>")
            .addTo(this.map);
        });
      }
    },
    toggleLayer() {
      let toggleVisibility = "";
      for (let i = 0; i < 3; i++) {
        toggleVisibility = i == this.selectedDataset ? "visible" : "none";
        this.map.setLayoutProperty(
          MapLayers.datasets[i].layers[0].id,
          "visibility",
          toggleVisibility
        );
      }
    }
  }
};
</script>

<style scoped>
.map {
  min-width: 100vw;
  min-height: 99vh;
  height: 100%;
  max-height: 100%;
  top: 0;
  left: 0;
  position: relative;
  z-index: 0;
}

.nav-drawer {
  position: absolute;
  background-color: white;
  bottom: 3vh;
  left: 1vh;
  width: 312px;
  z-index: 1;
}

.dataset-icon {
  position: absolute;
  top: 15vh;
  left: 5vw;
  z-index: 1;
  background-color: white;
}
</style>
